.navegacion {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 3rem;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 100;
  padding: 8rem 0 8rem 3rem;
  background-color: #000;
  transform: translateX(100%);
  transition-property: transform;
  transition-duration: 0.3s;
}
.navegacion[data-visible=true] {
  transform: translateX(0%);
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    position: unset;
    padding: 0;
    background-color: transparent;
    gap: 3.3rem;
    transform: translateX(0%);
  }
}
.navegacion__link {
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 300;
  position: relative;
  color: #fff;
  font-family: "Josefin Sans", sans-serif;
  font-size: 2.2rem;
}
@media (min-width: 768px) {
  .navegacion__link {
    text-transform: capitalize;
    letter-spacing: 0px;
    font-family: "Alata", sans-serif;
    font-size: 1.5rem;
    position: relative;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .navegacion__link::after {
    content: "";
    position: absolute;
    width: 2.4rem;
    height: 0.2rem;
    bottom: -0.8rem;
    left: 50%;
    background-color: #fff;
    transform: scaleX(0) translateX(-50%); /* desaparecemos la linea */
    transform-origin: left; /* nuestra transiccion sera llevada de escala 0 a 1, vamos a tener que saber desde donde se realizara esa transformacion */
    transition: transform 0.4s;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .navegacion__link:hover::after {
    left: 50%;
    transform: scaleX(1) translateX(-50%);
  }
}

