}
.creacion::after {
  content: "";
  background-image: linear-gradient(to right, rgba(255, 255, 255, 0.75) 0%, rgba(255, 255, 255, 0.75) 100%);
  position: absolute;
  inset: 0;
  transform: translateX(-100%);
  transition-property: transform;
  transition-duration: 0.5s;
}
.creacion:hover::after {
  transform: translateX(0);
}
